export const COLORS = {
  flower: {
    white: '#ffffff',
    blue: '#c9daf8',
    green: '#d9ead3',
    yellow: '#ffe599',
    orange: '#f9cb9c',
    pink: '#ead1dc',
    purple: '#d5a6bd',
    violet: '#b4a7d6',
    burgundy: '#a64d79'
  },
  foliage: {
    lightGreen: '#b6d7a8',
    darkGreen: '#6aa84f',
    grayGreen: '#d0e0e3',
    silverGreen: '#c9daf8',
    burgundy: '#a64d79'
  },
  gray: '#696969',
  lightGray: '#E5E5E5'
}

export function colorToHex(color) {
  switch(color) {
    case 'biela':
      return COLORS.flower.white;
    case 'modr':
      return COLORS.flower.blue;
    case 'zelen':
      return COLORS.flower.green;
    case 'lt':
      return COLORS.flower.yellow;
    case 'oranov':
      return COLORS.flower.orange;
    case 'ruov':
      return COLORS.flower.pink;
    case 'purpurov':
      return COLORS.flower.purple;
    case 'fialov':
      return COLORS.flower.violet;
    case 'bordov':
      return COLORS.flower.burgundy;
    case 'svetlozelen list':
      return COLORS.foliage.lightGreen;
    case 'tmavozelen list':
      return COLORS.foliage.darkGreen;
    case 'sivozelelen list':
      return COLORS.foliage.grayGreen;
    case 'strieborn list':
      return COLORS.foliage.silverGreen;
    case 'bordov list':
      return COLORS.foliage.burgundy;
    default:
      return null
  }
}
