// eslint-disable-next-line no-unused-vars
import { PlantItem } from "../models/PlantModel.mjs";

/**
 * @param {PlantItem} herbs
 */
export default function sortHerbs(herbs) {
  return herbs.sort((a, b) =>
    a.botanicalName > b.botanicalName
      ? 1
      : b.botanicalName > a.botanicalName
      ? -1
      : 0
  );
}
