import React, { useContext } from "react";
import { COLORS } from "../common/colors.mjs";
import TableItem from "./TableItem.js";

// eslint-disable-next-line no-unused-vars
import { Context, GlobalState } from "../services/GlobalStore.js";

// eslint-disable-next-line no-unused-vars
import { PlantItem } from "../models/PlantModel.mjs";
import { ReducerAction } from "../services/GlobalReducer.js";
import sortHerbs from "../utils/SortHerbs.js";

/**
 * @param {Object} props
 * @param {PlantItem[]} props.selectedHerbs
 * @param {PlantItem[]} props.suggestedHerbs
 */
const Delimiter = ({ selectedHerbs, suggestedHerbs }) => {
  const visible = selectedHerbs.length > 0 && suggestedHerbs.length > 0;
  return (
    visible && (
      <div
        style={{
          height: 1,
          backgroundColor: COLORS.lightGray,
          marginTop: 4,
          marginBottom: 4,
        }}
      />
    )
  );
};

export default function HerbsList() {
  /**
   * @type {[GlobalState, any]}
   */
  const [state, dispatch] = useContext(Context);

  const setSelectedHerbs = (herbs) => {
    const action = new ReducerAction("SET_SELECTED_PLANTS", herbs);
    dispatch(action);
  };

  const setSuggestedHerbs = (herbs) => {
    const action = new ReducerAction("SET_SUGGESTED_PLANTS", herbs);
    dispatch(action);
  };

  return (
    <div
      style={{
        minWidth: 800,
        maxWidth: 960,
        flex: 1,
        paddingTop: 30,
        marginBottom: 30,
        display: 'table'
      }}
    >
      {state.selectedHerbs.map((plant) => {
        return (
          <TableItem
            item={plant}
            key={plant.id}
            selected={true}
            onChange={(checked, item) => {
              const filteredSelected = state.selectedHerbs.filter(
                (filterItem) => {
                  return filterItem.id !== item.id;
                }
              );
              setSelectedHerbs(filteredSelected);
            }}
          />
        );
      })}
      <Delimiter
        selectedHerbs={state.selectedHerbs}
        suggestedHerbs={state.suggestedHerbs}
      />
      {state.suggestedHerbs.filter((plant) => {
        return !state.selectedHerbs.includes(plant)
      }).map((plant) => {
        return (
          <TableItem
            item={plant}
            key={plant.id}
            selected={false}
            onChange={(checked, item) => {
              const newSelected = [...state.selectedHerbs, item];
              setSelectedHerbs(sortHerbs(newSelected));
            }}
          />
        );
      })}
    </div>
  );
}
