import React, { useState } from "react";
import { Checkbox, Modal } from "antd";
import { COLORS } from "../common/colors.mjs";

// eslint-disable-next-line no-unused-vars
import { PlantItem } from "../models/PlantModel.mjs";
import { imagesDb } from "../db/images.js";

const handleSpace = (text) => (text !== "" ? text + "\xa0" : "");

function CircleElement({ color, text, small }) {
  return (
    <div
      style={{
        border: color ? "1px solid #D9D9D9" : "",
        width: 22,
        height: 22,
        backgroundColor: color,
        borderRadius: 12,
        fontSize: small ? 8 : 10,
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        marginLeft: 3,
        color: color === COLORS.flower.burgundy ? "#fff" : "#696969",
      }}
    >
      {text}
    </div>
  );
}

const colorPerMonth = (color, state) => (state !== 0 ? color : null);

/**
 *
 * @param {Object} param
 * @param {PlantItem} param.item
 * @param {boolean} param.selected
 * @param {function} param.onChange
 * @returns
 */
function TableItem({ item, selected, onChange }) {
  const [modalOpen, setModalOpen] = useState(false);
  const [highlighted, setHighlighted] = useState(false);
  return (
    <div
      style={{
        display: "flex",
        flexDirection: "row",
        justifyContent: "center",
        minWidth: 800,
      }}
    >
      <div
        style={{
          flex: 1,
          maxWidth: 960,
          fontSize: 12,
          height: 32,
          paddingLeft: 12,
          paddingRight: 12,
          display: "flex",
          flexDirection: "row",
          alignItems: "center",
          justifyContent: "center",
          borderRadius: 8,
          backgroundColor: highlighted ? COLORS.lightGray : 'inherit'
        }}
        onMouseEnter={() => {
          setHighlighted(true);
        }}
        onMouseLeave={() => {
          setHighlighted(false);
        }}
      >
        <Checkbox
          style={{
            marginRight: 10,
          }}
          onChange={(e) => {
            onChange(e.target.checked, item);
          }}
          checked={selected}
        />

        <div
          style={{
            marginRight: 0,
            display: "flex",
            flexDirection: "row",
            fontSize: 12,
            alignItems: "center",
            minWidth: 280,
            maxWidth: 600,
            flex: 1,
          }}
        >
          <div
            style={{
              cursor: "pointer",
            }}
            onClick={() => {
              setModalOpen(true);
            }}
          >
            <i>{handleSpace(item.genus)}</i>
            <i>{handleSpace(item.species)}</i>
            <i>{handleSpace(item.hybrid)}</i>
            <i>{handleSpace(item.subspecies)}</i>
            <b>{item.cultivar}</b>
          </div>
        </div>

        <div
          style={{
            display: "flex",
            flexDirection: "row",
            alignItems: "center",
            justifyContent: "space-between",
            width: 380,
            flexShrink: 0,
          }}
        >
          <CircleElement color={item.color} text="kvet" small />
          <CircleElement color={item.foliage} text="list" small />

          <CircleElement
            color={colorPerMonth(item.color, item.months.i)}
            text="I."
          />
          <CircleElement
            color={colorPerMonth(item.color, item.months.ii)}
            text="II."
          />
          <CircleElement
            color={colorPerMonth(item.color, item.months.iii)}
            text="III."
          />
          <CircleElement
            color={colorPerMonth(item.color, item.months.iv)}
            text="IV."
          />
          <CircleElement
            color={colorPerMonth(item.color, item.months.v)}
            text="V."
          />
          <CircleElement
            color={colorPerMonth(item.color, item.months.vi)}
            text="VI."
          />
          <CircleElement
            color={colorPerMonth(item.color, item.months.vii)}
            text="VII."
          />
          <CircleElement
            color={colorPerMonth(item.color, item.months.viii)}
            text="VIII."
          />
          <CircleElement
            color={colorPerMonth(item.color, item.months.ix)}
            text="IX."
          />
          <CircleElement
            color={colorPerMonth(item.color, item.months.x)}
            text="X."
          />
          <CircleElement
            color={colorPerMonth(item.color, item.months.xi)}
            text="XI."
          />
          <CircleElement
            color={colorPerMonth(item.color, item.months.xii)}
            text="XII."
          />
        </div>

        <div
          style={{
            fontSize: 10,
            width: 50,
            textAlign: "right",
            flexShrink: 0,
          }}
        >
          {item.height} cm
        </div>
      </div>

      <Modal
        centered
        open={modalOpen}
        onCancel={() => setModalOpen(false)}
        footer={null}
        width={288}
      >
        <i>{`${item.genus} ${item.species}`}</i><br/>
        <i>{`${item.hybrid} ${item.subspecies}`}</i><b>{item.cultivar}</b>
        <img
          width="240"
          height="240"
          src={imagesDb[item.id]}
          alt={item.botanicalName}
          style={{
            borderRadius: 4,
            marginTop: 8,
            marginBottom: 8
          }}
          />
          <a
            href={`https://www.google.com/search?tbm=isch&q=${item.botanicalName}`}
            target="_blank"
            rel="noopener noreferrer"
          >
            Viac obrzkov ...
          </a>
      </Modal>

    </div>
  );
}

export default TableItem;
