import { COLORS } from "../common/colors.mjs";
import { db } from "../db/db.js";

// eslint-disable-next-line no-unused-vars
import { FilterModel } from "../models/FilterModel.js";
// eslint-disable-next-line no-unused-vars
import { PlantItem, PLANT_ORIGIN, PLANT_USAGE } from "../models/PlantModel.mjs";

class DataService {
  /**
   * @param {string} query
   * @returns {PlantItem[]}
   */
  getSearch(query) {
    if (query.length > 2) {
      const genusBeginning = db.filter((item) =>
        item.genus.toUpperCase().startsWith(query.toUpperCase())
      );
      const speciesBeginning = db.filter((item) =>
        item.species.toUpperCase().startsWith(query.toUpperCase())
      );
      const hybridBeginning = db.filter((item) =>
        item.hybrid.toUpperCase().startsWith(query.toUpperCase())
      );
      const subspeciesBeginning = db.filter((item) =>
        item.subspecies.toUpperCase().startsWith(query.toUpperCase())
      );
      const cultivarBeginning = db.filter((item) =>
        item.cultivar.toUpperCase().startsWith(query.toUpperCase())
      );

      const genus = db.filter((item) =>
        item.genus.toUpperCase().includes(query.toUpperCase())
      );
      const species = db.filter((item) =>
        item.species.toUpperCase().includes(query.toUpperCase())
      );
      const hybrid = db.filter((item) =>
        item.hybrid.toUpperCase().includes(query.toUpperCase())
      );
      const subspecies = db.filter((item) =>
        item.subspecies.toUpperCase().includes(query.toUpperCase())
      );
      const cultivar = db.filter((item) =>
        item.cultivar.toUpperCase().includes(query.toUpperCase())
      );

      const fullSearch = db.filter((item) => 
        item.botanicalName.toUpperCase().includes(query.toUpperCase())
      );

      const filteredTmp = [
        ...genusBeginning,
        ...speciesBeginning,
        ...hybridBeginning,
        ...subspeciesBeginning,
        ...cultivarBeginning,
        ...genus,
        ...species,
        ...hybrid,
        ...subspecies,
        ...cultivar,
        ...fullSearch,
      ];

      const filtered = [...new Set(filteredTmp)];

      // Max amount of suggestions
      if (filtered.length > 8) {
        return filtered.slice(0, 8);
      }

      return filtered;
    } else {
      return [];
    }
  }

  /**
   * @param {FilterModel} filterModel
   */
  getByFilter(filterModel) {
    return db.filter(
      (item) => {
        const sunlight =
          (item.fs && filterModel.directSunlight) ||
          (item.ss && filterModel.penumbra) ||
          (item.sh && filterModel.shadow) ||
          (!filterModel.directSunlight && !filterModel.penumbra && !filterModel.shadow);

        const height =
          ((item.height <= (filterModel.maxHeight ? filterModel.maxHeight : 999)) &&
          item.height >= (filterModel.minHeight ? filterModel.minHeight : 0)) ||
         (filterModel.minHeight === null && filterModel.maxHeight === null);

        const color = 
          (filterModel.flowerWhite && COLORS.flower.white === item.color) ||
          (filterModel.flowerBlue && COLORS.flower.blue === item.color) ||
          (filterModel.flowerGreen && COLORS.flower.green === item.color) ||
          (filterModel.flowerYellow && COLORS.flower.yellow === item.color) ||
          (filterModel.flowerOrange && COLORS.flower.orange === item.color) ||
          (filterModel.flowerPink && COLORS.flower.pink === item.color) ||
          (filterModel.flowerPurple && COLORS.flower.purple === item.color) ||
          (filterModel.flowerViolet && COLORS.flower.violet === item.color) ||
          (filterModel.flowerBurgundy && COLORS.flower.burgundy === item.color) ||
          (!filterModel.flowerWhite && !filterModel.flowerBlue && !filterModel.flowerGreen && !filterModel.flowerYellow && !filterModel.flowerOrange && !filterModel.flowerPink && !filterModel.flowerPurple && !filterModel.flowerViolet && !filterModel.flowerBurgundy)

        const colorFoliage = 
          (filterModel.foliageLightGreen && COLORS.foliage.lightGreen === item.foliage) ||
          (filterModel.foliageDarkGreen && COLORS.foliage.darkGreen === item.foliage) ||
          (filterModel.foliageGrayGreen && COLORS.foliage.grayGreen === item.foliage) ||
          (filterModel.foliageSilverGreen && COLORS.foliage.silverGreen === item.foliage) ||
          (filterModel.foliageBurgundy && COLORS.foliage.burgundy === item.foliage) ||
          (!filterModel.foliageLightGreen && !filterModel.foliageDarkGreen && !filterModel.foliageGrayGreen && !filterModel.foliageSilverGreen && !filterModel.foliageBurgundy)

        const months = 
          (filterModel.january && item.months.i) ||
          (filterModel.february && item.months.ii) ||
          (filterModel.march && item.months.iii) ||
          (filterModel.april && item.months.iv) ||
          (filterModel.may && item.months.v) ||
          (filterModel.june && item.months.vi) ||
          (filterModel.july && item.months.vii) ||
          (filterModel.august && item.months.viii) ||
          (filterModel.september && item.months.ix) ||
          (filterModel.october && item.months.x) ||
          (filterModel.november && item.months.xi) ||
          (filterModel.december && item.months.xii) ||
          (!filterModel.january && !filterModel.february && !filterModel.march && !filterModel.april && !filterModel.may && !filterModel.june && !filterModel.july && !filterModel.august && !filterModel.september && !filterModel.october && !filterModel.november && !filterModel.december)

        const usage = 
          (filterModel.skeletalPerennial && item.usage === PLANT_USAGE.skeletalPerennial) ||
          (filterModel.groupPerennial && item.usage === PLANT_USAGE.groupPerennial) ||
          (filterModel.groundCoverPerennial && item.usage === PLANT_USAGE.groundCoverPerennial) ||
          (filterModel.interspersedPerennial && item.usage === PLANT_USAGE.interspersedPerennial) ||
          (filterModel.onion && item.usage === PLANT_USAGE.onion) ||
          (filterModel.springEphemera && item.usage === PLANT_USAGE.springEphemera) ||
          (!filterModel.skeletalPerennial && !filterModel.groupPerennial && !filterModel.groundCoverPerennial && !filterModel.interspersedPerennial && !filterModel.onion && !filterModel.springEphemera)

        const origin = 
          (filterModel.autochthonous && item.origin === PLANT_ORIGIN.autochthonous) ||
          (filterModel.naturalized && item.origin === PLANT_ORIGIN.naturalized) ||
          (filterModel.allochthonous && item.origin === PLANT_ORIGIN.allochthonous) ||
          (!filterModel.autochthonous && !filterModel.naturalized && !filterModel.allochthonous)
        
        const special = 
          (filterModel.botanicalType && item.hybrid === "" && item.cultivar === "") ||
          (filterModel.hybrid && item.hybrid !== "") ||
          (filterModel.cultivar && item.cultivar !== "") ||
          (!filterModel.botanicalType && !filterModel.hybrid && !filterModel.cultivar)
        
        const atLeastSomething = 
          filterModel.directSunlight ||
          filterModel.penumbra ||
          filterModel.shadow ||
          filterModel.minHeight ||
          filterModel.maxHeight ||
          filterModel.flowerWhite ||
          filterModel.flowerBlue ||
          filterModel.flowerGreen ||
          filterModel.flowerYellow ||
          filterModel.flowerOrange ||
          filterModel.flowerPink ||
          filterModel.flowerPurple ||
          filterModel.flowerViolet ||
          filterModel.flowerBurgundy ||
          filterModel.foliageLightGreen ||
          filterModel.foliageDarkGreen ||
          filterModel.foliageGrayGreen ||
          filterModel.foliageSilverGreen ||
          filterModel.foliageBurgundy ||
          filterModel.january ||
          filterModel.february ||
          filterModel.march ||
          filterModel.april ||
          filterModel.may ||
          filterModel.june ||
          filterModel.july ||
          filterModel.august ||
          filterModel.september ||
          filterModel.october ||
          filterModel.november ||
          filterModel.december ||
          filterModel.skeletalPerennial ||
          filterModel.groupPerennial ||
          filterModel.groundCoverPerennial ||
          filterModel.interspersedPerennial ||
          filterModel.onion ||
          filterModel.springEphemera ||
          filterModel.autochthonous ||
          filterModel.naturalized ||
          filterModel.allochthonous ||
          filterModel.botanicalType ||
          filterModel.hybrid ||
          filterModel.cultivar
        
        return sunlight && color && height && colorFoliage && months && usage && origin && special && atLeastSomething;
      }
      // (
      //   (item.fs && filterModel.directSunlight) ||
      //   (item.ss && filterModel.penumbra) ||
      //   (item.sh && filterModel.shadow)
      // ) && (
      // item.height <= (filterModel.maxHeight ? filterModel.maxHeight : 999) &&
      // item.height >= (filterModel.minHeight ? filterModel.minHeight : 0) &&
      // (filterModel.minHeight || filterModel.maxHeight)) &&
      // ((filterModel.flowerBlue && COLORS.flower.blue === item.color) ||
      //   (item.ss && filterModel.penumbra) ||
      //   (item.ss && filterModel.penumbra) ||
      //   (item.ss && filterModel.penumbra) ||
      //   (item.ss && filterModel.penumbra) ||
      //   (item.ss && filterModel.penumbra) ||
      //   (item.ss && filterModel.penumbra))
    );
  }
}

export const dataService = new DataService();
