// eslint-disable-next-line no-unused-vars
import { PlantItem } from "../models/PlantModel.mjs";
import sortHerbs from "../utils/SortHerbs.js";
// eslint-disable-next-line no-unused-vars
import { GlobalState } from "./GlobalStore.js";

export class ReducerAction {
  /**
   * @param {string} type
   * @param {any} payload
   */
  constructor(type, payload) {
    this.type = type;
    this.payload = payload;
  }
}

/**
 * @param {GlobalState} state
 * @param {ReducerAction} action
 * @returns
 */
const Reducer = (state, action) => {
  switch (action.type) {
    case "SET_SELECTED_PLANTS":
      return {
        ...state,
        selectedHerbs: action.payload,
      };
    case "SET_SUGGESTED_PLANTS":
      return {
        ...state,
        suggestedHerbs: action.payload,
      };
    case "ADD_SELECTED_PLANT":
      return {
        ...state,
        selectedHerbs: sortHerbs([...state.selectedHerbs, action.payload]),
      };
    default:
      return state;
  }
};

export default Reducer;
