import React, { useContext, useState } from "react";
import { AutoComplete, Typography } from "antd";

import "./EperHeader.css";
import { dataService } from "../services/DataService.mjs";
import { Context } from "../services/GlobalStore";
import { ReducerAction } from "../services/GlobalReducer";

const { Text } = Typography;

/**
 * Prepare Label with bold elements
 */
function BoldString({ text, boldText }) {
  if (text) {
    let stringMap = [];
    const indices = [...text.matchAll(new RegExp(boldText, "gi"))].map(
      (a) => a.index
    );
    for (let i = 0; i < indices.length; i++) {
      if (i === 0) {
        stringMap.push({ b: false, val: text.slice(0, indices[i]) });
      } else {
        stringMap.push({
          b: false,
          val: text.slice(indices[i - 1] + boldText.length, indices[i]),
        });
      }
      stringMap.push({
        b: true,
        val: text.slice(indices[i], indices[i] + boldText.length),
      });
    }
    stringMap.push({
      b: false,
      val: text.slice(indices[indices.length - 1] + boldText.length),
    });

    return (
      <div>
        {stringMap.map(({ b, val }) => {
          return (
            <Text key={Math.random()} strong={b}>
              {val}
            </Text>
          );
        })}
      </div>
    );
  } else {
    return;
  }
}

function EperHeader() {
  const [query, setQuery] = useState("");
  const [autocompleteOpen, setAutocompleteOpen] = useState(false);
  const [suggestions, setSuggestions] = useState([]);

  const [state, dispatch] = useContext(Context);

  const addToSelected = (item) => {
    dispatch(new ReducerAction('ADD_SELECTED_PLANT', item));
  }

  /**
   * @param {string} text
   */
  const handleSearch = (text) => {
    setQuery(text);
    const entries = dataService.getSearch(text);
    const suggestedItems = entries.map((entry) => {
      return renderItem(entry.id, entry.botanicalName, text, entry);
    });
    setSuggestions(suggestedItems);
  };

  const renderItem = (id, mTitle, mQuery, rawItem) => {
    return {
      value: id.toString(),
      label: <BoldString text={mTitle} boldText={mQuery} />,
      rawItem: rawItem
    };
  };

  return (
    <div className="container">
      <div className="row">
        <div
          className="title"
          onClick={() => {
            window.location.reload();
          }}
        >
          Eper
        </div>
        
        <div className="searchContainer">
          <AutoComplete
            allowClear
            style={{
              width: 420,
            }}
            options={suggestions}
            placeholder="njs rastlinu poda nzvu"
            value={query}
            onSearch={(text) => {
              handleSearch(text);
              setAutocompleteOpen(text.length > 2);
            }}
            onSelect={(key, value) => {
              console.log(key, value)
              addToSelected(value.rawItem);
              setQuery("");
              setAutocompleteOpen(false);
            }}
            open={autocompleteOpen}
            onBlur={() => {
              setAutocompleteOpen(false);
            }}
            onFocus={() => {
              setAutocompleteOpen(query.length > 2);
            }}
          />
        </div>
      </div>
      <div className="delimiter" />
    </div>
  );
}

export default EperHeader;
