/**
 * Create row in filter
 * @param {Object} props
 * @param {string} props.title
 * @param {((e: CheckboxChangeEvent) => void) | undefined} props.onChange
 */
function FilterRow({ title, onChange }) {
  return (
    <div
      style={{
        fontFamily: "Inter",
        color: "#696969",
        fontSize: 12,
        display: "flex",
        flexDirection: "row",
        alignItems: "center",
        height: 30,
      }}
    >
      <Checkbox
        onChange={onChange}
        style={{
          marginRight: 12,
        }}
      />
      {title}
    </div>
  );
}

/**
 * Filter row for height
 * @param {Object} props
 * @param {(type: string, value: number) => void} props.onChange
 * @param {number} props.minHeight
 * @param {number} props.maxHeight
 * @returns
 */
function HeightRow({onChange, minHeight, maxHeight }) {
  return (
    <div
      style={{
        display: "flex",
        flexDirection: "row",
      }}
    >
      min.
      <InputNumber
        style={{ width: 60, marginLeft: 3, marginRight: 3 }}
        size="small"
        min={0}
        max={maxHeight | 200}
        step={10}
        onChange={(value) => {
          onChange('MIN', value);
        }}
      />
      cm
      <div style={{ flex: 1 }} />
      max.
      <InputNumber
        style={{ width: 60, marginLeft: 3, marginRight: 3 }}
        size="small"
        min={minHeight | 0}
        max={200}
        step={10}
        onChange={(value) => {
          onChange('MAX', value);
        }}
      />
      cm
    </div>
  );
}

/**
 * Create base filter cathegory
 * @param {FilterViewModel} filterModel
 * @param {((index: number, state: boolean) => void) | undefined} onChange
 * @param {((type: string, value: number) => void) | undefined} onHeightChange
 * @param {string | number | undefined} key
 */
const renderFilterBaseCategory = (filterModel, onChange, onHeightChange, key) => (
  <Collapse.Panel
    style={{ fontFamily: "Inter", fontSize: 14 }}
    header={filterModel.title}
    key={key === undefined ? filterModel.title : key}
  >
    {filterModel.filterType === "basic" ? (
      filterModel.entries.map((item, index) => (
        <FilterRow
          key={index}
          title={item.title}
          onChange={(e) => {
            onChange(index, e.target.checked);
          }}
        />
      ))
    ) : (
      <HeightRow onChange={(type, value) => {
        onHeightChange(type, value);
      }}/>
    )}
  </Collapse.Panel>
);

export function FilterMenu() {
  /**
  * @type {[GlobalState, any]}
  */
  const [state, dispatch] = useContext(Context);

  const onUpdate = () => {
    let data = dataService.getByFilter(filterModel);
    for (let herb of state.selectedHerbs) {
      data = data.filter((suggested) => suggested.id !== herb.id)
    }
    dispatch(new ReducerAction('SET_SUGGESTED_PLANTS', data));
  }

  return (
    <Collapse
      style={{ margin: 30 }}
      expandIconPosition="end"
      expandIcon={({ isActive }) => (
        <CaretDownOutlined rotate={isActive ? 180 : 0} />
      )}
    >
      {filterData.map((filterRow) =>
        renderFilterBaseCategory(filterRow, (selectedIndex, state) => {
          console.log(filterRow.entries[selectedIndex].title + " " + state);
          filterModel.setValue(filterRow.entries[selectedIndex].title, state);
          onUpdate();
        }, (type, value) => {
          console.log(type, value);
          filterModel.setValue(type, value);
          onUpdate();
        })
      )}
    </Collapse>
  );
}
