import React, { createContext, useReducer, useMemo } from "react";
import Reducer from "./GlobalReducer";

// eslint-disable-next-line no-unused-vars
import { PlantItem } from "../models/PlantModel.mjs";

export class GlobalState {
  /**
   * @param {PlantItem[]} selectedHerbs
   * @param {PlantItem[]} suggestedHerbs
   */
  constructor(selectedHerbs = [], suggestedHerbs = []) {
    this.selectedHerbs = selectedHerbs;
    this.suggestedHerbs = suggestedHerbs;
  }
}

const initialState = new GlobalState();

function Store({ children }) {
  const [state, dispatch] = useReducer(Reducer, initialState);
  const reducerWrapper = useMemo(() => [state, dispatch], [state, dispatch]);
  return <Context.Provider value={reducerWrapper}>{children}</Context.Provider>;
}

export const Context = createContext(initialState);

export default Store;
