export class PlantActiveMonths {
  /**
   * @param {number} i 
   * @param {number} ii 
   * @param {number} iii 
   * @param {number} iv 
   * @param {number} v 
   * @param {number} vi 
   * @param {number} vii 
   * @param {number} viii 
   * @param {number} ix 
   * @param {number} x 
   * @param {number} xi 
   * @param {number} xii 
   */
  constructor(i, ii, iii, iv, v, vi, vii, viii, ix, x, xi, xii) {
    this.i = i;
    this.ii = ii;
    this.iii = iii;
    this.iv = iv;
    this.v = v;
    this.vi = vi;
    this.vii = vii;
    this.viii = viii;
    this.ix = ix;
    this.x = x;
    this.xi = xi;
    this.xii = xii;
  }
}

// --------------------------------------------------------------------

/**
 * @typedef {{ 
*     perennial: 0,
*     groundCoverPerennial: 1,
*     herbaceousPerennial: 2,
*     evergreenSemiShrub: 3,
*     onion: 4
* }} PlantType
*/

/**
 * @enum {PlantType}
 */
export const PLANT_TYPE = {
  perennial: 0,
  groundCoverPerennial: 1,
  herbaceousPerennial: 2,
  evergreenSemiShrub: 3,
  onion: 4
}

/**
 * @param {string} rawType 
 * @returns {PlantType}
 */
export function mapPlantType(rawType) {
  switch (rawType) {
    case 'trvalka':
      return PLANT_TYPE.perennial;
    case 'pokryvn trvalka':
      return PLANT_TYPE.groundCoverPerennial;
    case 'trvnat trvalka':
      return PLANT_TYPE.herbaceousPerennial;
    case 'stlozelen poloker':
      return PLANT_TYPE.evergreenSemiShrub;
    case 'cibuovina':
      return PLANT_TYPE.onion;
    default:
      break;
  }
}

/**
 * @param {PlantType} plantType 
 */
export function plantTypeToString(plantType) {
  switch (plantType) {
    case plantType.perennial:
      return 'trvalka';
    case plantType.groundCoverPerennial:
      return 'pokryvn trvalka';
    case plantType.herbaceousPerennial:
      return 'trvnat trvalka';
    case plantType.evergreenSemiShrub:
      return 'stlozelen poloker';
    case plantType.onion:
      return 'cibuovina';
    default:
      return '';
  }
}

// --------------------------------------------------------------------

/**
 * @typedef {{ 
*     skeletalPerennial: 0,
*     groupPerennial: 1,
*     groundCoverPerennial: 2,
*     interspersedPerennial: 3,
*     onion: 4,
*     springEphemera: 5
* }} PlantUsage
*/

/**
 * @enum {PlantUsage}
 */
export const PLANT_USAGE = {
  skeletalPerennial: 0,
  groupPerennial: 1,
  groundCoverPerennial: 2,
  interspersedPerennial: 3,
  onion: 4,
  springEphemera: 5
}

/**
 * @param {string} rawType 
 * @returns {PlantType}
 */
export function mapPlantUsage(rawType) {
  switch (rawType) {
    case 'kostrov trvalka':
      return PLANT_USAGE.skeletalPerennial;
    case 'skupinov trvalka':
      return PLANT_USAGE.groupPerennial;
    case 'pokryvn trvalka':
      return PLANT_USAGE.groundCoverPerennial;
    case 'vtrsen trvalka':
      return PLANT_USAGE.interspersedPerennial;
    case 'cibuovina':
      return PLANT_USAGE.onion;
    case 'jarn efemra':
      return PLANT_USAGE.springEphemera;
    default:
      break;
  }
}

// --------------------------------------------------------------------

/**
 * @typedef {{ 
*     autochthonous: 0,
*     naturalized: 1,
*     allochthonous: 2,
* }} PlantOrigin
*/

/**
 * @enum {PlantOrigin}
 */
export const PLANT_ORIGIN = {
  autochthonous: 0,
  naturalized: 1,
  allochthonous: 2,
}

/**
 * @param {string} rawType 
 * @returns {PlantOrigin}
 */
export function mapPlantOrigin(rawType) {
  switch (rawType) {
    case 'autochtnny druh':
      return PLANT_ORIGIN.autochthonous;
    case 'naturalizovan druh':
      return PLANT_ORIGIN.naturalized;
    case 'alochtnny druh':
      return PLANT_ORIGIN.allochthonous;
    default:
      break;
  }
}

/**
 * @param {PlantOrigin} plantOrigin 
 */
export function plantOriginToString(plantOrigin) {
  switch (plantOrigin) {
    case plantOrigin.autochthonous:
      return 'autochtnny druh';
    case plantOrigin.naturalized:
      return 'naturalizovan druh';
    case plantOrigin.allochthonous:
      return 'alochtnny druh';
    default:
      return '';
  }
}

// --------------------------------------------------------------------

export class PlantItem {
  /**
   * @param {number} id 
   * @param {string} botanicalName 
   * @param {string} genus 
   * @param {string} species 
   * @param {string} hybrid 
   * @param {string} subspecies 
   * @param {string} cultivar 
   * @param {PlantType} type 
   * @param {PlantActiveMonths} months 
   * @param {PlantUsage} usage 
   * @param {number} height 
   * @param {boolean} fs 
   * @param {boolean} ss 
   * @param {boolean} sh 
   * @param {boolean} color 
   * @param {string | null} foliage 
   * @param {PlantOrigin} origin 
   * @param {string} note 
   */
  constructor(
    id,
    botanicalName,
    genus,
    species,
    hybrid,
    subspecies,
    cultivar,
    type,
    months,
    usage,
    height,
    fs,
    ss,
    sh,
    color,
    foliage,
    origin,
    note
  ) {
    this.id = id;
    this.botanicalName = botanicalName;
    this.genus = genus;
    this.species = species;
    this.hybrid = hybrid;
    this.subspecies = subspecies;
    this.cultivar = cultivar;
    this.type = type;
    this.months = months;
    this.usage = usage;
    this.height = height;
    this.fs = fs;
    this.ss = ss;
    this.sh = sh;
    this.color = color;
    this.foliage = foliage;
    this.origin = origin;
    this.note = note;
  }
}
